.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
    }

    ul{
        margin: 30px;
        padding: 0;
        list-style: none;
        display: flex;

        li{
            font-size: 14px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: black;
                color: white;

            }

        }

    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 350px;
            height: 250px;
            border-radius: 20px;
            border: 1px solid lightgrey;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;

            h3{
                position: absolute;
                font-size: 20px;

            }

            img{
                height: 100%;
                width:100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: black;
                img{
                    opacity:0.2;

                }
            }
        }
    }


}